#popup {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    backdrop-filter: blur(5px);
    z-index: 2;

    #background {
        display: flex;
        justify-content: space-around;
        align-content: space-around;
        align-items: center;
        height: 100vh;
        width: 100vw;
        background-color: rgba(0, 0, 0, 0.5);

        .box_regular {
            display: flex;
            align-items: center;
            flex-direction: column;
            color: white;
            width: 65.6vw;
            height: 34vh;
            border-radius: 12px;
            background-color: white;
            opacity: 1; }

        .box_login {
            display: none;
            align-items: center;
            flex-direction: column;
            color: white;
            width: 65.6vw;
            height: 39.778vh;
            border-radius: 12px;
            background-color: white;
            opacity: 1; }

        .box_delete {
            display: none;
            align-items: center;
            flex-direction: column;
            color: white;
            width: 65.6vw;
            height: 34vh;
            border-radius: 12px;
            background-color: white;
            opacity: 1; }

        .message {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 84.78%;
            border-bottom: 0.3px solid #B3B3B3;

            p {
                color: black;
                font-size: 12px;
                margin: 3px; }

            p:nth-child(1) {
                font-size: 17px;
                margin: 15px 0; }

            figure {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 100%;
                height: auto;
                margin: 0; } }

        .ConfirmOrDelete {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 5.17vh;

            .ConfirmBox, .DeleteBox {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                width: 50%;
